<template>
  <div :class="['upload-wrap', {'upload-file-wrap': typeFile !== 'img'}]">
    <a href="javascript:void(0)"
       :class="typeFile !== 'img' ? 'file' : 'file-box'"
       :style="`width:${width}px;height:${height}px`"
    >
      <!-- 图片显示tag -->
      <img :src="fileUrl" alt="" class="img-style" v-if="typeFile === 'img'">
      <span :class="['handle-wrap', {'add-file': !fileUrl}]"
            :style="`width:${width}px;height:${height}px`"
      >
        <!-- 上传主体 start -->
        <el-upload
         ref="uploadRef"
         class="upload-util"
         v-loading.lock="uploadLoading"
         :action="updateUrl"
         :name="name"
         :limit="limit"
         :accept="accept"
         :multiple="multiple"
         :show-file-list="false"
         :on-success="uploadSuccess"
         :before-upload="beforeUpload"
         :on-progress="uploadProgress"
         :on-exceed="uploadExceed"
         :on-remove="removeUpload"
         :on-error="uploadFail"
         :style="`left:${width && (width-100)/2}px;`"
        >
          <template v-if="typeFile === 'img'"> <!-- 上传图片时icon -->
            <i class="el-icon-edit edit-btn" v-if="fileUrl"></i>
            <i class="el-icon-plus" v-else :style="`line-height:${height}px;`"></i>
          </template>
          <span v-else> <!-- 上传文件时icon -->
            <span class="file-name" v-if="fileUrl">{{fileName}}</span>
            <a href="javascript:void(0)" class="upload-file" v-else>
              <i class="el-icon-upload"></i>
              <span>选择上传文件</span>
            </a>
          </span>
        </el-upload>
        <!-- 上传主体 end -->
        <!-- 图片处理icon start -->
        <template v-if="typeFile === 'img'">
          <i class="el-icon-zoom-in edit-btn" @click="showFile(fileUrl)"></i> <!-- 查看 -->
          <i v-if="delIcon" class="el-icon-delete edit-btn" @click="delFile(replaceIndex, 'img')"></i> <!-- 删除 -->
        </template>
        <!-- 图片处理icon end -->
      </span>
    </a>
    <!-- 文件操作 start -->
    <div class="file-info-static" v-if="typeFile !== 'img' && fileUrl">
      <a :href="fileUrl" target="_blank" :download="fileName">查看</a>
      <span v-if="delIcon" @click="delFile(replaceIndex, 'file')">/ 删除</span>
      <slot name="fileHandle"></slot>
    </div>
    <!-- 文件操作 end -->
    <!-- 图片操作 start -->
    <div class="file-info-wrap" v-else :style="`width:${width}px;`">
      <slot name="fileHandle"></slot>
    </div>
    <!-- 图片操作 end -->
  </div>
</template>

<script>
import { updateUrl } from '@/service/common'
export default {
  name: 'uploadFile',
  props: {
    fileUrl: String, // 文件地址
    fileName: String, // 文件名
    name: { // 接口所需参数
      type: String,
      default: 'img'
    },
    typeFile: { // 当前上传文件类型，默认图片
      type: String,
      default: 'img'
    },
    accept: { // 上传文件类型
      type: String,
      default: 'image/png, image/jpeg'
    },
    multiple: { // 多文件上传
      type: Boolean,
      default: false
    },
    size: Number, // 文件限制大小
    limit: { // 文件个数限制
      type: Number
    },
    replaceIndex: Number, // 当前上传文件在数组中的位置，文件列表可用
    fileUrlArr: { // 已经上传了的文件数组，限制传几个文件时，必传参数
      type: Array,
      default: () => []
    },
    params: { // form表单组件传入需处理参数
      type: [Object, String, Number]
    },
    width: {
      type: [Number, String],
      default: ''
    },
    height: {
      type: [Number, String],
      default: ''
    },
    delIcon: { // 显示隐藏删除功能
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      updateUrl, // 接口地址
      uploadLoading: false,
      len: 0,
      limitLen: 0,
      urls: [],
      files: []
    }
  },
  methods: {
    /**
     * 图片上传前准备
     * @param  {Object} file     准备上传的文件信息
     * @return {Boolean}         返回真值时才可上传文件
     */
    beforeUpload (file) {
      this.len += 1
      this.limitLen = this.len
      const size = this.size ? this.size : 5
      const isLt = file.size / 1024 / 1024 < size
      if (!isLt) {
        this.$message.error(`上传文件大小不能超过 ${size}M!`)
      }
      return isLt
    },
    /**
     * 图片上传超出limit执行取消上传
     * @param  {Array} file     文件列表
     * @param  {Array} fileList [description]
     */
    uploadExceed (file, fileList) {
      if (file.length) {
        for (let i = 0; i < file.length; i++) {
          this.$refs.uploadRef.abort(file[i])
        }
      }
      this.$message.error(`最多只能上传${this.limit}个文件!`)
    },
    /**
     * 图片上传进度，多文件上传时需在这里判断一次已上传的文件列表是否超限
     * @param  {Object} e        文件上传进度信息
     * @param  {Object} file     当前上传文件信息
     * @param  {Array} fileList 文件列表
     */
    uploadProgress (e, file, fileList) {
      const i = this
      /* const lens = this.fileUrlArr.length
      if (i.multiple && i.limit &&
          ((i.limit === lens) && (i.len + lens > i.limit + 1)) ||
          ((i.limit !== lens) && (i.len + lens > i.limit))) {
        // 多文件上传超过限制limit, 阻止上传
        i.limitLen--
        this.uploadExceed([file])
      } else {
        // 无阻碍上传，加loading
        this.uploadLoading = true
      } */
      if (i.limitLen === -i.len) {
        // 清理上传失败文件队列
        this.len = 0
        this.$refs.uploadRef.clearFiles()
      }
      // i.progressVal = parseInt(e.percent) // 上传进度
    },
    removeUpload (file, fileList) { // 删除图片
    },
    /**
     * 图片上传成功触发
     * @param  {Object} res      返回成功上传信息
     * @param  {Object} file     当前上传成功的文件信息
     * @param  {Array} fileList 已经上传成功过的文件列表
     */
    uploadSuccess (res, file, fileList) {
      if (res.status === 'true') {
        if (this.len > 1) {
          this.len -= 1
          this.urls.push(res.info)
          this.files.push(file)
        } else {
          this.urls.push(res.info)
          this.files.push(file)
          this.$emit(
            'uploadSuccess',
            {
              urls: this.urls.join(','), // 上传成功的文件url字符串
              index: this.replaceIndex, // 当前需替换文件在文件数组中的位置
              files: this.multiple ? this.files : this.files[0], // 文件详细信息数组
              params: this.params // form表单内上传文件需特殊处理传值时传入
            }
          )
          this.len = 0
          this.urls = []
          this.files = []
          this.uploadLoading = false
          this.$refs.uploadRef.clearFiles()
          this.$message.success('文件上传成功！')
        }
      } else {
        this.$message.error(res.msg)
      }
    },
    /**
     * 上传失败触发，去loading效果
     * @param  {Object} file
     * @param  {Array} fileList
     */
    uploadFail (file, fileList) {
      this.uploadLoading = false
      this.$message.error('上传文件失败!')
    },
    /**
     * 预览图片
     * @param  {String} url 图片路径
     */
    showFile (url) {
      this.$alert(`<img src="${url}" />`, '', {
        dangerouslyUseHTMLString: true,
        showConfirmButton: false,
        closeOnClickModal: true,
        customClass: 'alert-style'
      })
    },
    /**
     * 删除文件
     * @param  {Number} index    文件在列表中的位置
     * @param  {String} fileType 文件类型
     */
    delFile (index, fileType) {
      this.$emit('delFile', {
        index: index,
        params: this.params,
        fileType: fileType
      })
    }
  }
}
</script>

<style lang="scss">
.file-box .add-file {
  .upload-util {
    .el-upload {
      display: block;
    }
  }
}
.el-upload {
  outline: none;
}
</style>
<style lang="scss" scoped>
  .upload-wrap {
    &.upload-file-wrap {
      display: flex;
      margin-bottom: 0;
    }
    .file {
      display: inline-block;
      height: 40px;
      margin-right: 15px;
      .edit-btn {
        display: none;
      }
      /* .file-name:hover {
        color: $theme-orange;
      } */
      .upload-file {
        padding: 10px 20px;
        box-sizing: border-box;
        color: #999;
        border: 1px solid #d8dce5;
        /* &:hover {
          border-color: $theme-orange;
          color: $theme-orange;
        } */
      }
    }
    .file-box {
      position: relative;
      display: inline-block;
      width: 100px;
      height: 100px;
      border: 1px solid #eaecf1;
      box-shadow: 0px 0px 10px #eaecf1;
      border-radius: 4px;
      overflow: hidden;
      .img-style {
        width: 100%;
        height: 100%;
      }
      .handle-wrap {
        position: absolute;
        top: 0;
        left: 0;
        width: 100px;
        height: 100px;
        display: flex;
        align-items: center;
        justify-content: space-around;
        .edit-btn {
          color: #fff;
          font-size: 18px;
          opacity: 0;
          transition: .3s opacity;
        }
        .el-icon-plus {
          display: none;
        }
      }
      .add-file {
        background-color: #fff;
        .edit-btn {
          display: none;
        }
        .upload-util {
          position: absolute;
          left: 0;
          top: 0;
          width: 100px;
          height: 100px;
          text-align: center;
          .el-icon-plus {
            display: block;
            color: #c1c5d0;
            font-size: 25px;
            line-height: 98px;
          }
        }
      }

      &:hover {
       /*  border-color: $theme-orange;
       color: $theme-orange; */
        .handle-wrap {
          background-color: rgba(0, 0, 0, 0.5);
          .edit-btn {
            opacity: 1;
            transition: .3s opacity;
          }
        }
        .add-file {
          background-color: #fff;
          /* .el-icon-plus {
            color: $theme-orange;
          } */
        }
      }
    }
    .file-info-static {
      color: $theme-orange;
      cursor: pointer;
      a {
        color: $theme-orange;
      }
    }
    .file-info-wrap {
      width: 100px;
      text-align: center;
      line-height: 1.5;
    }
  }
</style>



