import fetch from '../config/fetch'
import { API_PATH } from '../config/env'

// MBA项目列表
export const getMbaList = (ajaxParame) => fetch(API_PATH + '/activity/channelTeam/mbaManage/getList', ajaxParame, 'POST')

// 新建、编辑MBA项目
export const addMbaTeam = (ajaxParame) => fetch(API_PATH + '/activity/channelTeam/mbaManage/addOrUpdateTeam', ajaxParame, 'POST')

// MBA项目详情
export const getMbaDetail = (ajaxParame) => fetch(API_PATH + '/activity/channelTeam/mbaManage/getDetail', ajaxParame, 'GET')

// 删除MBA项目
export const delMbaTeam = (ajaxParame) => fetch(API_PATH + '/activity/channelTeam/mbaManage/delete', ajaxParame, 'GET')



