const mbaRoute = [
  {
    path: '/mba',
    name: '',
    component: () => import('@/pages/layout/layout'),
    meta: {
      defaultTarget: {name: '项目列表'} // 打开该路由的时候默认展开的下一级路由
    },
    children: [
      {
        path: 'list',
        name: '项目列表',
        component: () => import('@/pages/mba/list')
      },
      {
        path: 'edit',
        name: '',
        component: () => import('@/pages/mba/edit')
      }
    ]
  }
]
export default mbaRoute



