<!--富文本框-->
<template>
  <el-row style="margin-bottom: 40px">
    <el-col :span="20">
      <!--上传图片辅助控件-->
      <el-upload
        id="quill-upload"
        class="avatar-uploader"
        name="img"
        multiple
        :action="updateUrl"
        :show-file-list="false"
        :on-error="quillImgError"
        :before-upload="quillImgBeforeUpload"
        :on-success="quillImgSuccess">
      </el-upload>
      <el-form-item
        class="m-detail"
        :label="label">
        <div class="editor-container">
          <el-col
            v-loading="quillImgLoading"
            :span="24"
            class="quill-editor-box">
            <quill-editor
              :value="detail"
              ref="myQuillEditor"
              :options="editorOption"
              @change="onEditorChange($event)">
            </quill-editor>
          </el-col>
        </div>
      </el-form-item>
    </el-col>
  </el-row>
</template>

<script>
import { quillEditor } from 'vue-quill-editor'
import { toolbarOptions } from '@/config/utils'
import { updateUrl } from '@/service/common'
export default {
  name: 'qEditor',
  props: {
    detail: String,
    label: String,
    placeholder: String,
    params: String // 公共form表单组件需传入字段名
  },
  data () {
    return {
      updateUrl: updateUrl,
      quillImgLoading: false,
      // 富文本框参数设置
      editorOption: {
        placeholder: this.placeholder,
        theme: 'snow', // or 'bubble'
        modules: {
          toolbar: {
            container: toolbarOptions, // 工具栏
            handlers: {
              'image': function (value) {
                if (value) {
                  document.querySelector('#quill-upload input').click()
                } else {
                  this.quill.format('image', false)
                }
              }
            }
          }
        }
      }
    }
  },
  components: {
    quillEditor
  },
  methods: {
    /**
     * 富文本框内容改变时调用
     * @param  {Object} options.editor 编辑器信息
     * @param  {String} options.html   内容文本
     * @param  {[type]} options.text   [description]
     */
    onEditorChange ({editor, html, text}) {
      // this.form.detailContent = html
      this.$emit('changeDetail', html, this.params)
    },
    /**
     * 插入图片
     * @param  {Object} res  图片上传成功的返回信息
     * @param  {[type]} file [description]
     */
    quillImgSuccess (res, file) {
      let quill = this.$refs.myQuillEditor.quill
      if (res.code === '200' && res.info !== null) {
        let range = quill.getSelection()
        let length = range.index
        quill.insertEmbed(length, 'image', res.info)
        quill.setSelection(length + 1)
      } else {
        this.$message.warning('图片插入失败')
      }
      this.quillImgLoading = false
    },
    /**
     * 图片上传前加loading效果
     */
    quillImgBeforeUpload () {
      this.quillImgLoading = true
    },
    /**
     * 图片上传失败去loading，加提示
     */
    quillImgError () {
      this.quillImgLoading = false
      this.$message.error('图片插入失败')
    }
  }
}
</script>

<style lang="scss" scoped>
.avatar-uploader {
  display: none;
}
</style>



