// 路由文件示例

export default {
  path: '/example',
  // name: '路由示例',
  // redirect: '/example/children1',
  component: () => import('@/pages/example'),
  meta: {
    defaultTarget: {name: '路由示例2'} // 打开该路由的时候默认展开的下一级路由
  },
  children: [
    {
      path: 'children1',
      name: '路由示例2',
      component: () => import('@/pages/example/children1')
    },
    {
      path: 'children2',
      name: '路由示例3',
      component: () => import('@/pages/example/children2')
    }
  ]
}



