// import router from '../router'
import { login } from '@/service'

export default {
  // 用户登录，可以指定登录后的跳转
  // payload.redirect 登录后的跳转页
  /* login (ctx, payload) {
    ctx.commit('SET_TOKEN', 'tokentoken')
    ctx.commit('SET_HAS_LOGIN', true)
    ctx.commit('SET_USER_INFO', {name: 'test'})
    if (payload && payload.redirect) {
      router.push(payload.redirect)
    }
  }, */
  loginByAdmin ({ commit }, userInfo) {
    return new Promise((resolve, reject) => {
      login(userInfo).then(res => {
        if (res.status === 'true' && res.code === '200') {
          const data = res.info
          // sessionStorage.setItem('token', data.token)
          commit('SET_TOKEN', data.token)
          resolve(res)
        } else {
          reject(res)
        }
      }).catch(error => {
        reject(error)
      })
    })
  },
  // 用户注册
  register (ctx) {}
}



